import styled from "styled-components";

export const Main = styled.div`
  display: flex;
  flex-direction: column;
  .banner {
    width: 100%;
    height: 400px;
    background-image: url("https://i.mycdn.me/videoPreview?id=9034663199&type=47&idx=30&tkn=wipBQTGUxI8PKwWwvS831r2cnGg&i=1&fn=external_8");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% auto;
    border: none;
  }
  .hebrew {
    display: flex;
    text-align: right;
    direction: rtl;
    white-space: pre-wrap;
    width: 100%;
    margin-right: auto;
    padding: 5px;
    div {
      margin-left: 10px;
      width: 160px;
    }
  }
`;
